import Vue from 'vue'
import Router from 'vue-router'
import store from '../state/index'
import util from '../lib/util'
Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'Index',
    component: () => import('@/pages/Index'),
    meta: {
      title: '首页' // 需要被缓存
    }

  },
  {
    path: '/zizhushengbaoWeixian',
    name: 'zizhushengbaoWeixian',
    component: () => import('@/pages/IndependentDeclaration_weixian'),
    meta: {
      title: '危险申报',
      keepAlive: true // 需要被缓存
    }

  },
  {
    path: '/risingFalling',
    name: 'risingFalling',
    component: () => import('@/pages/IndependentDeclaration_risingFalling'),
    meta: {
      title: '来返义申报', // 需要被缓存
      keepAlive: true
    }
  },
  {
    path: '/goAndBackYiwu',
    name: 'goAndBackYiwu',
    component: () => import('@/pages/IndependentDeclaration_goAndBackYiwu'),
    meta: {
      title: '外出申报',
      keepAlive: true // 需要被缓存
    }
  },
  {
    path: '/declarationBayonet',
    name: 'declarationBayonet',
    component: () => import('@/pages/IndependentDeclaration_declarationBayonet'),
    meta: {
      title: '卡口申报',
      keepAlive: true // 需要被缓存
    }
  },
  {
    path: '/shangyu',
    name: 'shangyu',
    component: () => import('@/pages/IndependentDeclaration_shangyu'),
    meta: {
      title: '上虞申报',
      keepAlive: true // 需要被缓存
    }
  },
  {
    path: '/success',
    name: 'success',
    component: () => import('@/components/success'),
    meta: {
      title: '外出申报' // 需要被缓存
    }
  },
  {
    path: '/truckDeclaration',
    name: 'truckDeclaration',
    component: () => import('@/pages/IndependentDeclaration_truckDeclaration'),
    meta: {
      title: '货车申报' // 需要被缓存
    }
  }
  ]
})
export default router



